.plantilla-table{
    padding: 10px 20px;
}

.fixed-label-table{
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
    background-color: whitesmoke;
}

#custom-table {
    border-collapse: collapse;
    width: 100%;
}
  
#custom-table td, #custom-table th {
    text-align: start;
    border: 1px solid #ddd;
    padding: 8px;
    color: rgba(70, 70, 70, 0.8);
}

#custom-table th {
   font-size: 14px;
   color: #4276A4;
}

  #custom-table th button{
    display: inline-flex;
    align-items: center;
    color: #4276A4;
    background-color: transparent;
    border: none;
    padding: 2px;
  }

#custom-table th button:hover{
    transition: .3s;
    color: #1DA1F2;
}

#custom-table .trClass{
    font-size: small; 
}

#custom-table .trClass:hover, .column-option button:hover{
    background-color: #ddd;  
}

.column-option{
    align-content: center;
}

.column-option .inline-div-td-2{
    position: relative;
    float: right;
}

.column-option .inline-div-td-1{
    float: left;
}

.column-option button{
    display: flex;
    align-items: center;
    border: none;
    padding: 0px;
    background-color: transparent;
}


.column-option button:hover{
    color: #1DA1F2;
}

.scrollable-div-table{
    border: 1px solid rgba(122, 122, 122, 0.1);
    scroll-behavior: smooth;
    max-height: 60vh;
    overflow-y: scroll;
}

.data-entry{
    font-size: 12px;
    margin-top: 10px;
    color: rgba(54, 58, 63, 0.6);

}