.ul-dropdown-container{
    position: absolute;
    background-color: white;
    padding: 4px 0px;
    top: 26px;
    right: -3px;
    box-shadow: 0 0px 20px rgb(0 0 0 / 0.1);
    text-decoration: none;
    z-index: 1;
}

.ul-menu-item{
    padding: 7px 20px;
}
.ul-menu-item:hover{
    background-color: rgba(0, 110, 255, 0.1);
}

.ul-menu-item-arrow{
    margin: 0;
    position: absolute;
    top: -9px;
    right: 4px;
    color: white;
}

.ul-menu-item a{
    color: black;
    text-decoration: none;
}